.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-wobble infinite alternate-reverse 1s ease-in-out;
  height: 12vmin;
  padding-right: 10px;
}

.App-bg {
  display: block;
  width: 100%;
  position: absolute;
  z-index: 1;
  -webkit-transform: scaleX(1.01) scaleY(1.01);
          transform: scaleX(1.01) scaleY(1.01);
  -webkit-filter: blur(5px);
          filter: blur(5px);
}

.App-header {
  padding-top: 20px;
  height: 20vh;
  display: flex;
  position: relative;
  overflow: hidden;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 1.5vmin);
  color: rgb(255, 255, 255);
  box-sizing: border-box;
}
.App-form {
  height: 80vh;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-wobble {
  from { -webkit-transform: translate(0, 0px) scaleY(1); transform: translate(0, 0px) scaleY(1); }
  to { -webkit-transform: translate(0, -5px) scaleY(1.05); transform: translate(0, -5px) scaleY(1.05);}
}

@keyframes App-logo-wobble {
  from { -webkit-transform: translate(0, 0px) scaleY(1); transform: translate(0, 0px) scaleY(1); }
  to { -webkit-transform: translate(0, -5px) scaleY(1.05); transform: translate(0, -5px) scaleY(1.05);}
}

